<!-- -->
<template>
  <div class="container-fluid">
    <div class="row">
      <div class="col-sm-3 col-md-2 sidebar">
        <h3></h3>
        <br>
        <ul class="nav nav-sidebar">
          <li id="req_num_a" @click="goTag('#req_num')" class=""><a></a></li>
        </ul>
      </div>
      <div class="col-sm-9 col-sm-offset-3 col-md-10 col-md-offset-2 main">
        <h1 class="page-header"> </h1>

        <div class="form-container col-sm-12 col-md-11">
          <!---->
          <div id="req_num" class="form" ></div>
        </div>
      </div>
    </div>
  </div>

</template>
<script>
  import bus from '@/assets/eventBus'
  export default {
    name: "kfzj",
    data() {
      return {
        data: {
          req_num: {},
        },
      }
    },
    mounted() {
      // showLoading
      this.draw_req_num();

      /**/
      this.getData(localStorage.getItem('startTime'), localStorage.getItem('endTime'));
      bus.$on('time', (val) => {
        this.req_num.showLoading();

        let startTime = val[0];
        let endTime = val[1];
        this.getData(startTime, endTime);
      });
    },
    methods: {
      getData(startTime='default_start', endTime='default_end') { // 
        this.axios.get('/speech_recog',
          {
            params: {
              page: 'kfzj',
              start: startTime,
              end: endTime
            }
          }).then(res =>{
          this.data = res.data;
          // 
          this.fillData();

        }).catch(err =>{
          console.log(err);
        });
      },
      draw_req_num() { // 
        this.req_num = this.$echarts.init($("#req_num")[0]);
        // 1
        this.req_num.setOption({
          title: {
            text: '',
            textStyle: {
              color:'#533',
              fontFamily:'sans-serif'
            },
            subtext:'',
            subtextStyle:{
              color:'#534',
              fontSize:16
            }
          },
          tooltip: {trigger: 'axis',
            axisPointer: {
              type: 'cross',
              label: {
                backgroundColor: '#6a7985'
              }
            }},
          legend: {
            right:0,
            top:50,
            orient:'vetical',
            data:['']
          },
          toolbox: {
            show : true,
            feature : {
              dataView : {show: true, readOnly: false},
              magicType : {show: true, type: ['line', 'bar']},
              restore : {show: true},
              saveAsImage : {show: true}
            }
          },
          calculable : true,
          xAxis: {
            type: 'category',
            data: []
          },
          yAxis: {type : 'value'},
          dataZoom: [
            {   // dataZoomx
              type: 'slider', //  dataZoom  slider  dataZoom 
              start: 0,      //  10% 
              end: 100         //  60% 
            }
          ],
          series: [{
            name: '',
            type: 'bar',
            smooth:'true',
            barWidth:'20%',
            barGap:'50%',
            data: [],
            markPoint : {
              label:{
                normal:{
                  position:'top',
                  textStyle:{
                    color:'#000'
                  }
                }
              },
              data : [
                {type : 'max', name: ''},
                {type : 'min', name: ''}
              ]
            },
            markLine : {
              data : [
                {type : 'average', name: ''}
              ]
            }
          }]
        });
        this.req_num.showLoading();
      },

      fillData() {
        this.req_num.hideLoading();

        this.req_num.setOption({
          xAxis: {
            data: this.data.date_day
          },
          series: [{
            // 
            name: '',
            data: this.data.day_req
          }]
        });
      }

    },
    destroyed() {
      bus.$off('time');
    }

  }
</script>

<style lang="less" scoped>
</style>





