            axisPointer: {
              type: 'cross',
              label: {
                backgroundColor: '#6a7985'
              }
            }},
          legend: {
            right:0,
            top:50,
            orient:'vetical',
            data:['Android', 'iOS', 'sum']
          },
          toolbox: {
            show : true,
            feature : {
              dataView : {show: true, readOnly: false},
              magicType : {show: true, type: ['line', 'bar']},
