<!-- -->
<template>
  <div class="container-fluid">
    <div class="row">
      <div class="col-sm-3 col-md-2 sidebar">
        <h3></h3>
        <br>
        <ul class="nav nav-sidebar">
          <li id="real_time_a" @click="goTag('#real_time')" class=""><a>PV</a></li>
          <li id="total_a" @click="goTag('#total')"><a>PV</a></li>
        </ul>
      </div>
      <div class="col-sm-9 col-sm-offset-3 col-md-10 col-md-offset-2 main">
        <h1 class="page-header"></h1>

        <div class="form-container col-sm-12 col-md-11">
          <!--PV-->
          <div id="real_time" class="form" ></div>
          <!--PV-->
          <div id="total" class="form"></div>
        </div>
      </div>
    </div>
  </div>

</template>

<script>
  import bus from '@/assets/eventBus'
  export default {
    name: "onlineSfc",
    data() {
      return {
        data: {
          real_time: {},
          total: {},
        },
      }
    },
    mounted() {
      // showLoading
      this.draw_real_time();
      this.draw_total();

      /**/
      this.getData(localStorage.getItem('startTime'), localStorage.getItem('endTime'));
      bus.$on('time', (val) => {
        this.real_time.showLoading();
        this.total.showLoading();

        let startTime = val[0];
        let endTime = val[1];
        this.getData(startTime, endTime);
      });
    },
    methods: {
      getData(startTime='default_start', endTime='default_end') { // 
        this.axios.get('/speech_compose',
          {
            params: {
              page: 'online_sfc',
              start: startTime,
              end: endTime
            }
          }).then(res =>{
          this.data = res.data;
          // 
          this.fillData();

        }).catch(err =>{
          console.log(err);
        });
      },
      draw_real_time() { // PV
        this.real_time = this.$echarts.init($("#real_time")[0]);
        this.real_time.setOption({
          title: {
            text: 'PV',
            textStyle: {
              color:'#533',
              fontFamily:'sans-serif'
            },
            subtext:'',
            subtextStyle:{
              color:'#534',
              fontSize:16
            }
          },
          tooltip: {trigger: 'axis'},
          legend: {
            right:0,
            top:50,
            orient:'vetical',
            data:[{name:'PV'}]
          },
          toolbox: {
            show : true,
            feature : {
              dataView : {show: true, readOnly: false},
              magicType : {show: true, type: ['line', 'bar']},
              restore : {show: true},
              saveAsImage : {show: true}
            }
          },
          calculable : true,
          xAxis: {
            type: 'category',
            data: []
          },
          yAxis: {type : 'value'},
          dataZoom: [
            {   // dataZoomx
              type: 'slider', //  dataZoom  slider  dataZoom 
              start: 0,      //  10% 
              end: 100         //  60% 
            }
          ],
          series: [
            {
              name:'PV',
              type:'bar',
              barGap:'20%',
              barCategoryGap:'50%',
              smooth:'true',
              data:[],
              markPoint : {
                label:{
                  normal:{
                    position:'top',
                    textStyle:{
                      color:'#000'
                    }
                  }
                },
                data : [
                  {type : 'max', name: ''},
                  {type : 'min', name: ''}
                ]
              },
              markLine : {
                data : [
                  {type : 'average', name: ''}
                ]
              }
            }]
        });
        this.real_time.showLoading();
      },
      draw_total() { // PV
        this.total = this.$echarts.init($("#total")[0]);

        this.total.setOption({
          title: {
            text: 'PV',
            textStyle: {
              color:'#533',
              fontFamily:'sans-serif'
            },
            subtext:'',
            subtextStyle:{
              color:'#534',
              fontSize:16
            }
          },
          tooltip: {trigger: 'axis'},
          legend: {
            right:0,
            top:50,
            orient:'vetical',
            data:[{name:'PV'}]
          },
          toolbox: {
            show : true,
            feature : {
              dataView : {show: true, readOnly: false},
              magicType : {show: true, type: ['line', 'bar']},
              restore : {show: true},
              saveAsImage : {show: true}
            }
          },
          calculable : true,
          xAxis: {
            type: 'category',
            data: []
          },
          yAxis: {type : 'value'},
          dataZoom: [
            {   // dataZoomx
              type: 'slider', //  dataZoom  slider  dataZoom 
              start: 0,      //  10% 
              end: 100         //  60% 
            }
          ],
          series: [
            {
              name:'PV',
              type:'bar',
              barGap:'20%',
              barCategoryGap:'50%',
              smooth:'true',
              data:[],
              markPoint : {
                label:{
                  normal:{
                    position:'top',
                    textStyle:{
                      color:'#000'
                    }
                  }
                },
                data : [
                  {type : 'max', name: ''},
                  {type : 'min', name: ''}
                ]
              },
              markLine : {
                data : [
                  {type : 'average', name: ''}
                ]
              }
            }]
        });
        this.total.showLoading();
      },

      fillData() {
        this.real_time.hideLoading();
        this.total.hideLoading();

        this.real_time.setOption({
          xAxis: {
            data: this.data.date_hour
          },
          series: [{
            // 
            name: 'PV',
            data: this.data.pv
          }
          ]
        });
        this.total.setOption({
          xAxis: {
            data: this.data.date_day
          },
          series: [{
            // 
            name: 'PV',
            data: this.data.sfc_pv
          }
          ]
        });

      }

    },
    destroyed() {
      bus.$off('time');
    }

  }
</script>

<style lang="less" scoped>
</style>



