import Vue from 'vue'
import Router from 'vue-router'

import notFound from '@/components/NotFound' // 404
import index from '@/components/index' // 

import kfPrediction from '../components/kfRelated/kfPrediction' // -
import commentTag from '../components/kfRelated/commentTag' // -

import judgeZcqx from '../components/requestForCancel/judgeZcqx' // -
import judgeZcjz from '../components/requestForCancel/judgeZcjz' // -

// 
import judgeKcqlqx from '../components/requestForCancel/judgeKcqlqx' // -
import judgeKcjz from '../components/requestForCancel/judgeKcjz' // -
import judgeKcss from '../components/requestForCancel/judgeKcss' // -
import judgeKcimqx from '../components/requestForCancel/judgeKcimqx' // -im

import judgeAqqd from '../components/requestForCancel/judgeAqqd' // -
import judgeAqhc from '../components/requestForCancel/judgeAqhc' // -

import judgeSfc from '../components/requestForCancel/judgeSfc' // -
import judgeDj from '../components/requestForCancel/judgeDj' // -

import judgeDjhs from '../components/requestForCancel/judgeDjhs' // -
import judgeZchs from '../components/requestForCancel/judgeZchs' // -

import driverBroadcast from '../components/speechCompose/driverBroadcast' // 
import onlineSfc from '../components/speechCompose/onlineSfc' // -
import onlineIvr from '../components/speechCompose/onlineIvr' // -ivr

import speechTotal from '../components/speechRecog/speechTotal' // -
import speechKc from '../components/speechRecog/speechKc' // --
import speechCzc from '../components/speechRecog/speechCzc' // --
import speechHhc from '../components/speechRecog/speechHhc' // --
import speechDj from '../components/speechRecog/speechDj' // --
import speechZc from '../components/speechRecog/speechZc' // --

import appJqr from '../components/speechRecog/appJqr' // --
import appRg from '../components/speechRecog/appRg' // --
import appDhrx from '../components/speechRecog/appDhrx' // --
import kfzj from '../components/speechRecog/kfzj' // --



Vue.use(Router);

export default new Router({
  // mode: 'hash',
  routes: [
    { path: '*', name: 'notFound',component: notFound }, //404
    { path: '/', name: 'index', component: index }, // 

    { path: '/kfPrediction', name: 'kfPrediction', component: kfPrediction }, // -
    { path: '/commentTag', name: 'commentTag', component: commentTag }, // -

    { path: '/judgeZcqx', name: 'judgeZcqx', component: judgeZcqx }, // -
    { path: '/judgeZcjz', name: 'judgeZcjz', component: judgeZcjz }, // -

    { path: '/judgeKcqlqx', name: 'judgeKcqlqx', component: judgeKcqlqx }, // -
    { path: '/judgeKcjz', name: 'judgeKcjz', component: judgeKcjz }, // -
    { path: '/judgeKcss', name: 'judgeKcss', component: judgeKcss }, // -
    { path: '/judgeKcimqx', name: 'judgeKcimqx', component: judgeKcimqx }, // -im

    { path: '/judgeAqqd', name: 'judgeAqqd', component: judgeAqqd }, // -
    { path: '/judgeAqhc', name: 'judgeAqhc', component: judgeAqhc }, // -

    { path: '/judgeSfc', name: 'judgeSfc', component: judgeSfc }, // -
    { path: '/judgeDj', name: 'judgeDj', component: judgeDj }, // -

    { path: '/judgeDjhs', name: 'judgeDjhs', component: judgeDjhs }, // -
    { path: '/judgeZchs', name: 'judgeZchs', component: judgeZchs }, // -

    { path: '/driverBroadcast', name: 'driverBroadcast', component: driverBroadcast }, // --
    { path: '/onlineSfc', name: 'onlineSfc', component: onlineSfc }, // -
    { path: '/onlineIvr', name: 'onlineIvr', component: onlineIvr }, // -ivr

    { path: '/speechTotal', name: 'speechTotal', component: speechTotal }, // -
    { path: '/speechKc', name: 'speechKc', component: speechKc }, // --
    { path: '/speechCzc', name: 'speechCzc', component: speechCzc }, // --
    { path: '/speechHhc', name: 'speechHhc', component: speechHhc }, // --
    { path: '/speechDj', name: 'speechDj', component: speechDj }, // --
    { path: '/speechZc', name: 'speechZc', component: speechZc }, // --

    { path: '/appJqr', name: 'appJqr', component: appJqr }, // --
    { path: '/appRg', name: 'appRg', component: appRg }, // --
    { path: '/appDhrx', name: 'appDhrx', component: appDhrx }, // --
    { path: '/kfzj', name: 'kfzj', component: kfzj }, // --

  ],

  mode: 'hash',
  base: __dirname
})




