          </ul>
          <div class="nav navbar-form navbar-right time-picker">
            <el-date-picker
            v-model="timeValue"
            type="daterange"
            align="center"
            unlink-panels
            range-separator="-"
            start-placeholder=""
            end-placeholder=""
            :picker-options="pickerOptions"
            size="mini"
            popper-class="time-popper"
            :change="changeTime()"
