<script>
  import bus from '@/assets/eventBus'
  export default {
    name: "sheader",
    mounted() {
      $('.nav .router-link').on('click', function(){
        // $('.navbar-toggle').click() //bootstrap 3.x by Richard
        if($('.navbar-toggle').css('display') !== 'none'){
          $('.navbar-toggle').trigger( "click" );
        }
      });
      localStorage.clear();
      localStorage.setItem("startTime","default_start");
      localStorage.setItem("endTime","default_end");
      localStorage.setItem("timeValue", '');

      /**/
      let timeList = ['default_start','default_end'];
      bus.$emit('time', timeList);
    },
    watch: { // 
      '$route' (to, from) {
        console.log(from);
        this.gotop();
      }
    },
    data() {
      return {
        pageName: {
          'index': '',
          'notFound': '404',

          'kfPrediction': '',
          'commentTag': '',
          'judgeZcqx': '',
          'judgeZcjz': '',

          'judgeKcqlqx': '',
          'judgeKcjz': '',
          'judgeKcss': '',
          'judgeKcimqx': 'IM',

          'judgeAqqd': '',
          'judgeAqhc': '',

          'judgeSfc': '',
          'judgeDj': '',

          'judgeDjhs': '',
          'judgeZchs': '',

          'driverBroadcast': '',
          'onlineSfc': '',
          'onlineIvr': 'IVR',

          'speechTotal': '',
          'speechKc': '',
          'speechCzc': '',
          'speechHhc': '',
          'speechDj': '',
          'speechZc': '',

          'appJqr': 'APP',
          'appRg': 'APP',
          'appDhrx': '',
          'kfzj': '',
        },
        pickerOptions: { // 
          shortcuts: [{
            text: '',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
              end.setTime(end.getTime() - 3600 * 1000 * 24 );
              picker.$emit('pick', [start, end]);
            }
          }, {
            text: '',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
              picker.$emit('pick', [start, end]);
            }
          }, {
            text: '',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
              picker.$emit('pick', [start, end]);
            }
          }]
        },
        timeValue: '' // datePicker
      }
    },
    methods: {
      gotop() {
        // 
        document.body.scrollTop = 0;
        document.documentElement.scrollTop = 0;
      },
      changeTime() {
        if (this.timeValue) {
          let newTime = this.timeValue[0] + ',' + this.timeValue[1];
          // changeemit
          if (newTime !== localStorage.getItem("timeValue")) {
            // 
            if($('.navbar-toggle').css('display') !== 'none'){
              $('.navbar-toggle').trigger( "click" );
            }

            let today = new Date();
            if (this.timeValue[0] > today) { // 

              // 
              $('#modal').modal('show');

              this.timeValue = '';
              localStorage.setItem("startTime", "default_start");
              localStorage.setItem("endTime", "default_end");

              /**/
              let timeList = ['default_start', 'default_end'];
              bus.$emit('time', timeList);

            }
            else {
              if (this.timeValue[1] > today) { // 
                this.timeValue[1] = today;
              }
              let startYear = this.timeValue[0].getFullYear();
              let startMonth = this.timeFormat(this.timeValue[0].getMonth() + 1);
              let startDate = this.timeFormat(this.timeValue[0].getDate());
              let endYear = this.timeValue[1].getFullYear();
              let endMonth = this.timeFormat(this.timeValue[1].getMonth() + 1);
              let endDate = this.timeFormat(this.timeValue[1].getDate());
              let startTime = startYear + startMonth + startDate;
              let endTime = endYear + endMonth + endDate;

              localStorage.setItem("startTime", startTime);
              localStorage.setItem("endTime", endTime);

              localStorage.setItem("timeValue", this.timeValue);

              /**/
              let timeList = [startTime, endTime];
              bus.$emit('time', timeList);
            }
          }
        }
      },
      timeFormat(value) { // 0
        if (parseInt(value) < 10) {
          return '0' + value
        }
        else
          return value;
      }
    }
  }
</script>

<style scoped>
  .navbar-header, ul, li {
    cursor: pointer;
  }

  /*hover*/
  ul.nav li.dropdown:hover > ul.dropdown-menu {
    display: block;
  }
  /**/
  .dropdown-submenu {
    position: relative;
  }
  .dropdown-submenu > .dropdown-menu {
    top: 0;
    left: 100%;
    margin-top: -6px;
    margin-left: -1px;
    -webkit-border-radius: 0 6px 6px 6px;
    -moz-border-radius: 0 6px 6px;
    border-radius: 0 6px 6px 6px;
  }
  .dropdown-submenu:hover > .dropdown-menu {
    display: block;
  }
  .dropdown-submenu > a:after {
    display: block;
    content: " ";
    float: right;
    width: 0;
    height: 0;
    border-color: transparent;
    border-style: solid;
    border-width: 5px 0 5px 5px;
    border-left-color: #ccc;
    margin-top: 5px;
    margin-right: -10px;
  }
  .dropdown-submenu:hover > a:after {
    border-left-color: #fff;
  }
  .dropdown-submenu.pull-left {
    float: none;
  }
  .dropdown-submenu.pull-left > .dropdown-menu {
    left: -100%;
    margin-left: 10px;
    -webkit-border-radius: 6px 0 6px 6px;
    -moz-border-radius: 6px 0 6px 6px;
    border-radius: 6px 0 6px 6px;
  }
  /**/
  /*.time-picker {*/
    /*!*font-size: 14px;*!*/
    /*!*width: 0;*!*/
    /*margin: auto 0;*/
    /*line-height: 50px;*/
    /*height: 50px;*/
  /*}*/
  /**/
  /*.time-popper {*/
    /*width: 100px;*/
    /*height: 100px;*/
  /*}*/

  /**/
  @media (max-width: 1085px) {
    .navbar-header {
      float: none;
    }
    .navbar-toggle {
      display: block;
    }
    .navbar-collapse {
      border-top: 1px solid transparent;
      box-shadow: inset 0 1px 0 rgba(255,255,255,0.1);
    }
    .navbar-collapse.collapse {
      display: none!important;
    }
    .navbar-nav {
      float: none!important;
      margin: 7.5px -15px;
    }
    .navbar-form {
      float: none!important;
      margin: 7.5px -15px;
    }
    .navbar-nav>li {
      float: none;
    }
    .navbar-nav>li>a {
      padding-top: 10px;
      padding-bottom: 10px;
    }
    .navbar-text {
      float: none;
      margin: 15px 0;
    }
    /* since 3.1.0 */
    .navbar-collapse.collapse.in {
      display: block!important;
    }
    .collapsing {
      overflow: hidden!important;
    }
  }
</style>



